import {
  ApiAxiosInstance,
  MockDataAxiosInstance,
} from '../AxiosInstance/AxiosInstance'
import { isUsingMockData } from '../../config/AppConfig'

/**
 * Classe de base pour les services.
 */
export class BaseService {
  /**
   * Rcupre des donnes  partir d'une API en fonction de l'URL de l'endpoint fourni.
   * @param {string} endpoint - L'URL de l'endpoint  partir duquel rcuprer les donnes.
   * @returns {Promise<Object>} Les donnes rcupres depuis l'API.
   * @throws {Error} Une erreur si la rcupration des donnes choue.
   */
  async fetchData(endpoint) {
    try {
      // Utilise l'instance Axios approprie en fonction de l'utilisation de donnes simules ou relles.
      const response = await (
        isUsingMockData ? MockDataAxiosInstance : ApiAxiosInstance
      ).get(endpoint)
      return response.data
    } catch (error) {
      // En cas d'erreur, affiche un message d'erreur et lance l'erreur.
      console.error(`Error while retrieving data from ${endpoint}`, error)
      throw error
    }
  }
}
