import { BaseService } from '../BaseService/BaseService'

/**
 * Classe de service pour fournir des donnes simules.
 */
export class MockService extends BaseService {
  /**
   * Rcupre les donnes utilisateur simules pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes utilisateur simules.
   * @throws {Error} Une erreur si les donnes utilisateur ne sont pas trouves dans les donnes simules.
   */
  async getUserData(userId) {
    try {
      const data = await this.fetchData()
      const userData = data[userId]?.userInfo
      if (userData) {
        return { data: userData }
      } else {
        throw new Error(
          'Donnes utilisateur non trouves dans les donnes simules',
        )
      }
    } catch (error) {
      throw new Error(
        'Erreur lors de la rcupration des donnes utilisateur depuis les donnes simules : ' +
          error.message,
      )
    }
  }

  /**
   * Rcupre les donnes d'activit utilisateur simules pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes d'activit utilisateur simules.
   * @throws {Error} Une erreur si les donnes d'activit utilisateur ne sont pas trouves dans les donnes simules.
   */
  async getUserActivity(userId) {
    try {
      const data = await this.fetchData()
      const userActivity = data[userId]?.userActivity
      if (userActivity) {
        return { data: userActivity }
      } else {
        throw new Error(
          "Donnes d'activit utilisateur non trouves dans les donnes simules",
        )
      }
    } catch (error) {
      throw new Error(
        "Erreur lors de la rcupration des donnes d'activit utilisateur depuis les donnes simules : " +
          error.message,
      )
    }
  }

  /**
   * Rcupre les donnes de sessions moyennes simules pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes de sessions moyennes simules.
   * @throws {Error} Une erreur si les donnes de sessions moyennes ne sont pas trouves dans les donnes simules.
   */
  async getUserAverageSessions(userId) {
    try {
      const data = await this.fetchData()
      const averageSessions = data[userId]?.averageSessions
      if (averageSessions) {
        return { data: averageSessions }
      } else {
        throw new Error(
          'Donnes de sessions moyennes utilisateur non trouves dans les donnes simules',
        )
      }
    } catch (error) {
      throw new Error(
        'Erreur lors de la rcupration des donnes de sessions moyennes depuis les donnes simules : ' +
          error.message,
      )
    }
  }

  /**
   * Rcupre les donnes de performance utilisateur simules pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes de performance utilisateur simules.
   * @throws {Error} Une erreur si les donnes de performance utilisateur ne sont pas trouves dans les donnes simules.
   */
  async getUserPerformance(userId) {
    try {
      const data = await this.fetchData()
      const performance = data[userId]?.performance
      if (performance) {
        return { data: performance }
      } else {
        throw new Error(
          'Donnes de performance utilisateur non trouves dans les donnes simules',
        )
      }
    } catch (error) {
      throw new Error(
        'Erreur lors de la rcupration des donnes de performance depuis les donnes simules : ' +
          error.message,
      )
    }
  }
}
