import { useState, useEffect, useContext } from 'react'
import { DataContext } from '../../utils/context/DataContext'

/**
 * Hook personnalis pour rcuprer les donnes d'hbergement d'un utilisateur.
 * @param {string} userId - L'identifiant de l'utilisateur.
 * @returns {Object} Un objet contenant les donnes d'utilisateur, d'activit, de sessions moyennes,
 * de performance utilisateur, l'tat de chargement et l'ventuelle erreur.
 */
const useHostingData = (userId) => {
  const { dataService } = useContext(DataContext)
  const [userData, setUserData] = useState(null)
  const [userActivity, setUserActivity] = useState(null)
  const [averageSessions, setAverageSessions] = useState(null)
  const [userPerformance, setUserPerformance] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    /**
     * Fonction asynchrone pour rcuprer les donnes d'utilisateur, d'activit,
     * de sessions moyennes et de performance utilisateur.
     */
    const fetchData = async () => {
      try {
        const userData = await dataService.getUserData(userId)
        const userActivity = await dataService.getUserActivity(userId)
        const averageSessions = await dataService.getUserAverageSessions(userId)
        const userPerformance = await dataService.getUserPerformance(userId)

        // Met  jour les tats avec les donnes rcupres
        setUserData(userData)
        setUserActivity(userActivity)
        setAverageSessions(averageSessions)
        setUserPerformance(userPerformance)
        setLoading(false)
      } catch (error) {
        // Gre les erreurs de rcupration des donnes
        console.error('Erreur lors de la rcupration des donnes :', error)
        setError(error)
        setLoading(false)
      }
    }

    // Appelle la fonction de rcupration des donnes
    fetchData()

    // Nettoyage des tats lors du dmontage du composant
    return () => {
      setUserData(null)
      setUserActivity(null)
      setAverageSessions(null)
      setUserPerformance(null)
      setLoading(true)
      setError(null)
    }
  }, [dataService, userId])

  // Retourne les donnes d'utilisateur, d'activit, de sessions moyennes,
  // de performance utilisateur, l'tat de chargement et l'ventuelle erreur
  return {
    userData,
    userActivity,
    averageSessions,
    userPerformance,
    loading,
    error,
  }
}

export default useHostingData
