import APIDataAdapter from './APIDataAdapter'
import MockDataAdapter from './MockDataAdapter'
import { isUsingMockData } from '../../config/AppConfig'

/**
 * Classe utilitaire pour adapter les donnes en fonction du mode d'utilisation des donnes.
 */
class DataAdapter {
  /**
   * Adapte les donnes utilisateur en fonction du mode d'utilisation des donnes.
   * @param {Object} data - Les donnes utilisateur  adapter.
   * @returns {Object} Les donnes utilisateur adaptes.
   */
  static adaptUserData(data) {
    const adapter = isUsingMockData ? MockDataAdapter : APIDataAdapter
    try {
      return adapter.adaptUserData(data)
    } catch (error) {
      console.error('Error adapting user data:', error)
      return error
    }
  }

  /**
   * Adapte les donnes d'activit utilisateur en fonction du mode d'utilisation des donnes.
   * @param {Object} data - Les donnes d'activit utilisateur  adapter.
   * @returns {Object[]} Les donnes d'activit utilisateur adaptes.
   */
  static adaptUserActivity(data) {
    const adapter = isUsingMockData ? MockDataAdapter : APIDataAdapter
    try {
      return adapter.adaptUserActivity(data)
    } catch (error) {
      console.error('Error adapting user activity data:', error)
      return error
    }
  }

  /**
   * Adapte les donnes de sessions moyennes en fonction du mode d'utilisation des donnes.
   * @param {Object} data - Les donnes de sessions moyennes  adapter.
   * @returns {Object[]} Les donnes de sessions moyennes adaptes.
   */
  static adaptAverageSessions(data) {
    const adapter = isUsingMockData ? MockDataAdapter : APIDataAdapter
    try {
      return adapter.adaptAverageSessions(data)
    } catch (error) {
      console.error('Error adapting average sessions data:', error)
      return error
    }
  }

  /**
   * Adapte les donnes de performances utilisateur en fonction du mode d'utilisation des donnes.
   * @param {Object} data - Les donnes de performances utilisateur  adapter.
   * @returns {Object[]} Les donnes de performances utilisateur adaptes.
   */
  static adaptUserPerformance(data) {
    const adapter = isUsingMockData ? MockDataAdapter : APIDataAdapter
    try {
      return adapter.adaptUserPerformance(data)
    } catch (error) {
      console.error('Error adapting user performance data:', error)
      return error
    }
  }
}

export default DataAdapter
