import PropTypes from 'prop-types'
import style from './Icon.module.scss'

// Composant Icon
const Icon = ({ IconComponent, alt }) => {
  return <IconComponent className={style.icon} aria-label={alt} />
}

// Dfinition des types de proprits attendues
Icon.propTypes = {
  IconComponent: PropTypes.elementType.isRequired, // Icne sous forme de composant
  alt: PropTypes.string.isRequired, // Texte alternatif pour l'icne
}

export default Icon
