import { ApiService } from '../ApiService/ApiService'
import { MockService } from '../MockService/MockService'
import { isUsingMockData } from '../../config/AppConfig'

/**
 * Classe de service pour grer les donnes utilisateur.
 */
class DataService {
  constructor() {
    // Initialise le service en fonction de l'utilisation des donnes simules ou relles.
    this.service = isUsingMockData ? new MockService() : new ApiService()
  }

  /**
   * Rcupre les donnes utilisateur pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes utilisateur.
   */
  async getUserData(userId) {
    return await this.service.getUserData(userId)
  }

  /**
   * Rcupre les donnes d'activit utilisateur pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes d'activit utilisateur.
   */
  async getUserActivity(userId) {
    return await this.service.getUserActivity(userId)
  }

  /**
   * Rcupre les donnes de sessions moyennes pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes de sessions moyennes.
   */
  async getUserAverageSessions(userId) {
    return await this.service.getUserAverageSessions(userId)
  }

  /**
   * Rcupre les donnes de performance utilisateur pour un utilisateur spcifi.
   * @param {number} userId - L'identifiant de l'utilisateur.
   * @returns {Promise<Object>} Les donnes de performance utilisateur.
   */
  async getUserPerformance(userId) {
    return await this.service.getUserPerformance(userId)
  }
}

export { DataService }
