import PropTypes from 'prop-types'
import Icon from '../Icon/Icon'

// Composant IconButton
const IconButton = ({ IconComponent, altText, onClick }) => {
  return (
    // Bouton avec icne
    <button onClick={onClick}>
      {/* Utilisation du composant Icon avec l'icne spcifie */}
      <Icon IconComponent={IconComponent} alt={altText} />
    </button>
  )
}

// Dfinition des types de proprits attendues
IconButton.propTypes = {
  IconComponent: PropTypes.elementType.isRequired, // Icne sous forme de composant
  altText: PropTypes.string.isRequired, // Texte alternatif pour l'icne
  onClick: PropTypes.func, // Gestionnaire d'vnements de clic (optionnel)
}

export default IconButton
