import PropTypes from 'prop-types'
import Icon from '../../Icon/Icon'
import { ReactComponent as CaloriesIcon } from '../../../assets/calories-icon.svg'
import { ReactComponent as ProteinIcon } from '../../../assets/protein-icon.svg'
import { ReactComponent as CarbsIcon } from '../../../assets/carbs-icon.svg'
import { ReactComponent as FatIcon } from '../../../assets/fat-icon.svg'
import style from './Result.module.scss'
import DataAdapter from '../../../utils/adapters/DataAdapter'
import NoData from '../../Error/NoData'

/**
 * Composant Result affichant les donnes nutritionnelles telles que les calories, les protines, les glucides et les lipides.
 * @param {Object} props - Les proprits passes au composant.
 * @param {Object} props.data - Les donnes nutritionnelles de l'utilisateur.
 * @param {string} props.category - La catgorie de donnes  afficher ('calories', 'proteines', 'glucides' ou 'lipides').
 * @returns {JSX.Element} Composant Result.
 */
const Result = ({ data, category }) => {
  // Adapter les donnes de l'utilisateur
  const adaptedData = DataAdapter.adaptUserData(data.data)

  // Dfinir les catgories de donnes avec les icnes associes
  const categories = {
    calories: {
      count: adaptedData.calorieCount,
      icon: CaloriesIcon,
      label: 'Calories',
      unit: 'kCal',
    },
    proteines: {
      count: adaptedData.proteinCount,
      icon: ProteinIcon,
      label: 'Protines',
      unit: 'g',
    },
    glucides: {
      count: adaptedData.carbohydrateCount,
      icon: CarbsIcon,
      label: 'Glucides',
      unit: 'g',
    },
    lipides: {
      count: adaptedData.lipidCount,
      icon: FatIcon,
      label: 'Lipides',
      unit: 'g',
    },
  }

  // Rcuprer les donnes de la catgorie spcifie
  const categoryData = categories[category]

  // Afficher un message d'erreur si la catgorie spcifie n'existe pas
  if (!categoryData) return null

  return (
    <div className={style.resultsDataItem}>
      {!categoryData.count ? (
        <NoData />
      ) : (
        <>
          {/* Afficher l'icne de la catgorie */}
          <div className={style.iconWrapper}>
            <Icon
              IconComponent={categoryData.icon}
              alt={`${categoryData.label} Icon`}
            />
          </div>
          {/* Afficher le nombre et l'unit de la catgorie */}
          <div>
            <span>
              {formatDataCount(categoryData.count)}
              {categoryData.unit}
            </span>
            <p>{categoryData.label}</p>
          </div>
        </>
      )}
    </div>
  )
}

/**
 * Formater le nombre de donnes en utilisant un format spcifique.
 * @param {number} dataCount - Le nombre de donnes  formater.
 * @returns {string|null} Le nombre format ou null si le nombre est inexistant.
 */
const formatDataCount = (dataCount) => {
  return dataCount
    ? new Intl.NumberFormat('en-IN', { maximumSignificantDigits: 4 }).format(
        dataCount,
      )
    : null
}

// Dfinir les types de proprits attendues
Result.propTypes = {
  data: PropTypes.shape({
    data: PropTypes.object.isRequired,
  }).isRequired, // Les donnes nutritionnelles de l'utilisateur
  category: PropTypes.oneOf(['calories', 'proteines', 'glucides', 'lipides'])
    .isRequired, // La catgorie de donnes  afficher
}

export default Result
